.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
  color: $color-default-white;
  background: conic-gradient(from 151deg at 16.03% 39.06%, #003D85 32.23633825778961deg, #B80027 313.3060026168823deg);


  @include vp-1439 {
    padding: 30px $header-container-side-padding;
  }

  @include vp-1200 {
    padding: 40px 45px;
  }

  @include vp-767 {
    padding: 15px 15px 80px 15px;
  }
}
